





























import Vue from "vue";
import { getAllTopStories } from "@/api/api";
import { Article } from "@/types/hackernews";


export default Vue.extend({
  name: "QuotableQuotes",
  data: function () {
    return {
      articles: Array<Article>()
    };
  },
  async mounted() {
    this.articles = await getAllTopStories();
  }
});

