


























import Vue from "vue";
import api from "@/api/iss";

export default Vue.extend({
  name: "ISS",
  data: function () {
    return {
      name: "",
      main: "",
      astronauts: Array<string>()
    };
  },
  async mounted() {
    this.main = await api.main();
    this.astronauts = await api.getAstronauts();
  }
});

