






























import Vue from "vue";
import { getAllQuotableQuotes } from "@/api/api";
import {Quote} from "@/types/quotes";


export default Vue.extend({
  name: "QuotableQuotes",
  data: function () {
    return {
      quotes: Array<Quote>()
    };
  },
  async mounted() {
    this.quotes = await getAllQuotableQuotes();
  }
});

