


























import Vue from "vue";
import {getRandomQuotableQuote} from "@/api/api";

export default Vue.extend({
  name: "Home",
  data: function () {
    return {
      content: "",
      author: ""
    };
  },
  async mounted() {
    const quote = await getRandomQuotableQuote();
    this.content = quote.content;
    this.author = quote.author;
    console.log(this.content, this.author);
  }
});

